.container {
    margin: 0;
    
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;


}
.home {
    display: flex;
    margin-top: 50px;
    justify-content: center;

}

.homeButton {
    margin-right: 50px;
    max-width: 250px;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-weight: 500;
    display: flex;
    font-size: 30px;
    padding: 5px 10px;
    width: 50%;
    min-width: 100px;
    color: black;
    background-color: #3b578f;
    margin-bottom: 10px;
    border-radius: 5px;
    user-select: none;
    cursor: pointer;
}
.title {
    display: flex;
    flex-direction: column;
    padding: 30px;
    outline: 5px solid rgb(32, 32, 32);
    justify-content: center;
    color: rgb(255, 255, 255);
    text-align: center;
    align-items: center;
    
}

.resetButton {
    margin-top: 15px;
    background-color: #3b578f;
    width: 15%;
    border-radius: 5px;
    height: 50px;
    
}

@media screen and (min-height: 300px) {
    .mainContainer {
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .container {
        margin-top: 50px;
        margin-bottom: 10px;
    }
}


@media screen 
 and (max-width: 1400px) 
 and (min-width: 700px) 
{ 
    .container {

        margin-top: 50px;
        margin-bottom: 50px;

        width: 100%;
    
    }
    .mainContainer {
        margin: 25px;
        width: 50%;
        display: flex;
        flex-direction: row;
        flex: 1;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

}

    


.mainContainer {
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex: 1 0 11%;
    flex-wrap: wrap;
    width: 50%;

}

.counter {
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-wrap: wrap;

    user-select: none;



}
.count {

    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-width: 100px;
    background-color: white;
    color: black;
    font-weight: 700;
    width: 50%;
    font-size: 30px;
    padding: 10px;
    margin-bottom: 10px;
}
.btnContainer {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.button {
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 30px;
    padding: 5px 10px;
    width: 25%;
    min-width: 100px;
    color: black;
    background-color: #3b578f;
    margin-bottom: 10px;
    border-radius: 5px;
    user-select: none;
    cursor: pointer;

}